  margin-bottom: 15px;
}
/*
 * cardboxs style
 */
.cardbox-container {
  margin-bottom: 10px;
}

.cardbox-container h2 {
  font-weight: bold;
  width: 100px;
  min-height: 70px;
  text-align: center;
}
@media(min-width: 992px){
  .cardbox-container h2 {
    width: 130px;
  }
}

.cardIsHover:hover{
  box-shadow: 0px 0px 20px 5px #c80020;
}

.cardConfirmed {
  box-shadow: 0px 0px 20px 5px #7ec500;
}

.cardbox {
  border: 2px solid #0F1593;
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
}
.cardbox .inner {
  margin: 3px 3px 0;
  border-radius: 12px;
  background-image: linear-gradient(to bottom, #0F1593, #161ec9);
  background-image: -webkit-linear-gradient(top, #0F1593, #161ec9);
  text-align: center;
}

.card-background-color{
  background-image: linear-gradient(to bottom, #0F1593, #161ec9);
  background-image: -webkit-linear-gradient(top, #0F1593, #161ec9);
}

.cardbox-label {
  display: block;
  font-size: 2em;
  font-weight: bold;
  padding: 50px 0 50px;
  color: #FFFFFF;
}

.disable-card{
  pointer-events: none;
  opacity: 0.5;
}

@media (min-width: 992px) {
  .cardbox-label {
    padding: 75px 0 75px;
  }
}

.cardbox, .cardbox-flip {
    height: 158px;
    width: 100px;
}
@media (min-width: 992px) {
  .cardbox, .cardbox-flip {
    height: 208px;
    width: 130px;
  }
}


/* cardbox flip */
.cardbox-overview {
  perspective: 1000px;
}
.cardbox-overview:first-child {
  margin-left: 15px;
}
.cardbox-overview .cardbox-container {
  float: left;  
  width: 33%;
}
@media(min-width: 768px) {
  .cardbox-overview {
    margin-right: -40px;
  }
  .cardbox-overview .cardbox-container {
    width: 160px;
  }
}
@media(min-width: 992px) {
  .cardbox-overview {
    margin-right: -60px;
  }
  .cardbox-overview .cardbox-container {
    width: 195px;
  }
}
@media(min-width: 1200px) {
  .cardbox-overview .cardbox-container {
    width: 210px;
  }
}
.cardbox.front, .cardbox.back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 15px;
  left: 0;
}
.deletable-cardbox{
  position: relative;
  height: 212px;
  width: 142px;
}
.cardbox.back {
  transform: rotateY(180deg);
}
.cardbox-flip{
  transition: 1s;
  transform-style: preserve-3d;
  position: relative;
}
.cardbox-flip.flipped {
  transform: rotateY(180deg);
}

.delete-member {
  top: -6px;
  left: 140px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.leave {
  top: 10px;
  left: 5px;
  height: 40px;
  width: 40px;
}
  
.remove {
  display: block; 
  position: absolute; 
  color: white;
  text-shadow: 0 1px rgba(0, 0, 0, 0.25);
  border: 1px solid;
  border-radius: 3px;
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.3), 0 1px 1px rgba(0, 0, 0, 0.08);
  -webkit-box-shadow: inset 0 1px rgba(255, 255, 255, 0.3), 0 1px 1px rgba(0, 0, 0, 0.08);
  background: #fa623f;
  border-color: #fa5a35;
  background-image: linear-gradient(to bottom, #fc9f8a, #fa623f);
  background-image: -webkit-linear-gradient(top, #fc9f8a, #fa623f);
}

.remove span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
}